import * as React from "react"
import { MdArrowForward as Arrow } from "react-icons/md"
import { ButtonStyles } from "./ButtonStyles"

const Button = ({ text, to, as, arrow, onClick, type }) => {
    return (
        <ButtonStyles onClick={(e) => onClick && onClick(e)} className="btn" as={as} to={to} type={type}>
            {text}
            {arrow || to ? <Arrow style={{ marginLeft: "10px" }} /> : null}
        </ButtonStyles>
    )
}

export default Button
