import { basePrice, isNullOrUndefined, registerArtist, registerBasePrice } from "../constants/globals"

export class ArtWorkPicture {
    static prefix = "/assets/cloudinary/"

    image_id

    constructor(image_id) {
        this.image_id = image_id
    }

    _to_path(subdir) {
        return ArtWorkPicture.prefix + subdir + "/" + this.image_id + ".webp"
    }

    to_featured_path() {
        return this._to_path("featured")
    }

    to_original_path() {
        return this._to_path("original")
    }

    to_banner_path() {
        return this._to_path("banner")
    }

    to_framed_path() {
        return this._to_path("framed")
    }
}

export class ArtWork {
    slug
    title
    introduction
    year
    artWorkType
    color
    description
    height
    width
    medium
    minimumPrice
    picture
    saleOptions
    sellingPrice

    constructor(
        slug,
        title,
        introduction,
        year,
        artWorkType,
        color,
        description,
        height,
        width,
        medium,
        minimumPrice,
        picture,
        saleOptions,
        sellingPrice
    ) {
        this.slug = slug
        this.title = title
        this.introduction = introduction
        this.year = year
        this.artWorkType = artWorkType
        this.color = color
        this.description = description
        this.height = height
        this.width = width
        this.medium = medium
        this.minimumPrice = minimumPrice
        this.picture = new ArtWorkPicture(picture[0].public_id)
        this.saleOptions = saleOptions
        this.sellingPrice = sellingPrice
    }

    isSold() {
        return this.saleOptions.includes("sold")
    }

    getPrice() {
        if (this.isSold() && !isNullOrUndefined(this.sellingPrice)) {
            return this.sellingPrice
        }
        if (isNullOrUndefined(this.minimumPrice)) {
            return basePrice()
        } else {
            return this.minimumPrice
        }
    }

    static fromContentful(contentfulArtWork, contentfulArtist) {
        if (!isNullOrUndefined(contentfulArtist)) {
            Artist.registerFromContentful(contentfulArtist)
        }
        let artWork = new ArtWork(
            contentfulArtWork.slug,
            contentfulArtWork.title,
            contentfulArtWork.introduction.introduction,
            contentfulArtWork.year,
            contentfulArtWork.artWorkType,
            contentfulArtWork.color,
            contentfulArtWork.description,
            contentfulArtWork.height,
            contentfulArtWork.width,
            contentfulArtWork.medium,
            contentfulArtWork.minimumPrice,
            contentfulArtWork.picture,
            contentfulArtWork.saleOptions,
            contentfulArtWork.sellingPrice
        )
        artWork.gatsbyImage = contentfulArtWork.gatsbyImage
        return artWork
    }
}

export class Artist {
    name
    slug
    domain
    about
    bio
    metadata
    instagramUsername
    artwork
    featuredArtwork

    constructor(name, slug, domain, about, bio, metadata, instagramUsername, artwork, featuredArtwork) {
        this.name = name
        this.about = about
        this.slug = slug
        this.domain = domain
        this.bio = bio
        this.metadata = metadata
        this.instagramUsername = instagramUsername
        this.artwork = artwork.map((work) => {
            return ArtWork.fromContentful(work)
        })
        this.featuredArtwork = featuredArtwork.map((featured) => {
            return this.artwork.find((w) => w.slug === featured.slug)
        })
    }

    artworkBySlug(slug) {
        return this.artwork.find((artwork) => artwork.slug === slug)
    }

    static registerFromContentful(contentfulArtist) {
        if (!isNullOrUndefined(contentfulArtist)) {
            const artist = this.fromContentful(contentfulArtist)
            registerBasePrice(contentfulArtist.metadata.campaignConfiguration.basePrice)
            return registerArtist(artist)
        } else {
            const artist = new Artist()
            return registerArtist(artist)
        }
    }

    static fromContentful(contentfulArtist) {
        if (isNullOrUndefined(contentfulArtist)) {
            return null
        }
        return new Artist(
            contentfulArtist.name,
            contentfulArtist.slug,
            contentfulArtist.domain,
            contentfulArtist.about.about,
            contentfulArtist.bio,
            contentfulArtist.metadata,
            contentfulArtist.instagram,
            contentfulArtist.artwork,
            contentfulArtist.featuredArtWork
        )
    }
}
