import * as React from "react"
import { Helmet } from "react-helmet"
import { currentArtist, getEnv, isNullOrUndefined, isProduction } from "../constants/globals"

const SEO = ({ title, artist }) => {
    if (isNullOrUndefined(artist)) {
        artist = currentArtist()
    }
    if (isNullOrUndefined(artist)) {
        return <></>
    }

    const metadata = artist.metadata
    const description = metadata.description.description
    const image = metadata.image
    const metaImage = image.metaImage
    const imageUrl = metaImage.images.fallback.src
    const imageWidth = metaImage.width
    const imageHeight = metaImage.height
    const imageAlt = image.description || image.title
    const siteUrl = "https://" + artist.domain
    const baseDomain = artist.domain.substring(4)
    title = (title ? title + "- " : "") + metadata.title

    if (!isProduction()) {
        title += " - " + getEnv()
    }

    return (
        <Helmet htmlAttribute={{ lang: "en" }} title={`${title}`}>
            <meta name="description" content={description} />
            <meta name="image" content={imageUrl} />

            {/* Opengraph, e.g. Facebook Card */}
            <meta property="og:url" content={siteUrl} />
            <meta property="og:type" content="website" />
            {/* TODO i18n */}
            <meta property="og:locale" content="en_US" />
            <meta property="og:title" content={title} />
            <meta property="og:description" content={description} />
            <meta property="og:image" content={imageUrl} />
            <meta property="og:image:alt" content={imageAlt} />
            <meta property="og:image:width" content={imageWidth} />
            <meta property="og:image:height" content={imageHeight} />
            <meta property="dev:env" content={getEnv()} />

            {/* Twitter Card
      <meta name="twitter:card" content="summary_large_image" />
      <meta name="twitter:creator" content={twitterUsername} />
      <meta name="twitter:title" content={siteTitle} />
      <meta name="twitter:description" content={siteDesc} />
      <meta name="twitter:image" content={`${siteUrl}${image}`} />
      */}

            {/* handle https://github.com/vercel/next.js/issues/9070 and https://plausible.io/docs/spa-support */}
            {/* TODO use artist domain */}
            {isProduction() && (
                <script
                    defer
                    data-domain={baseDomain}
                    src="https://analytics.thefamouscat.com/js/plausible.js"
                ></script>
            )}
        </Helmet>
    )
}

export default SEO
