import Bugsnag from "@bugsnag/js"
import BugsnagPluginReact from "@bugsnag/plugin-react"

let _artist = null
let _basePrice = 600

export function registerBasePrice(basePrice) {
    _basePrice = basePrice
}

export function basePrice() {
    return _basePrice
}

export function registerArtist(artist) {
    _artist = artist
    return artist
}

export function currentArtist() {
    return _artist
}

export function isNullOrUndefined(param) {
    return typeof param === "undefined" || param === null
}

export function isBuilding() {
    return process.env.GATSBY_IS_BUILDING === "true"
}

export function getEnv() {
    let env
    if (!isNullOrUndefined(process.env.GATSBY_ENV)) {
        env = process.env.GATSBY_ENV.trim()
    } else {
        env = process.env.NODE_ENV.trim()
    }
    return env
}

export function isProduction() {
    return getEnv() === "production"
}

Bugsnag.start({
    apiKey: "cb11cfa7b20b9131b55b562f21ac9b9b",
    plugins: [new BugsnagPluginReact()],
    releaseStage: isBuilding() || getEnv(),
    enabledReleaseStages: ["production", "staging", "development"],
})

export function isOnClient() {
    return typeof window !== "undefined"
}

export function setStorageObject(key, object) {
    if (!isOnClient()) {
        return
    }
    window.localStorage.setItem(key, JSON.stringify(object))
}

function saveGetItem(key) {
    try {
        return window.localStorage.getItem(key)
    } catch (e) {
        return null
    }
}

function saveSetItem(key, value) {
    try {
        window.localStorage.setItem(key, value)
    } catch (e) {
        //ignore
    }
}

export function partialUpdateStorageObject(key, partial) {
    if (!isOnClient()) {
        return
    }
    if (isNullOrUndefined(saveGetItem(key))) {
        saveSetItem(key, JSON.stringify(partial))
    } else {
        const obj = JSON.parse(saveGetItem(key))
        for (let k in partial) {
            obj[k] = partial[k]
        }
        saveSetItem(key, JSON.stringify(obj))
    }
}

export function getStorageObject(key) {
    if (!isOnClient() || isNullOrUndefined(saveGetItem(key))) {
        return null
    } else {
        return JSON.parse(saveGetItem(key))
    }
}

export const isMobile = {
    Android: function () {
        return isOnClient() && navigator.userAgent.match(/Android/i)
    },
    BlackBerry: function () {
        return isOnClient() && navigator.userAgent.match(/BlackBerry/i)
    },
    iOS: function () {
        return isOnClient() && navigator.userAgent.match(/iPhone|iPad|iPod/i)
    },
    Opera: function () {
        return isOnClient() && navigator.userAgent.match(/Opera Mini/i)
    },
    Windows: function () {
        return isOnClient() && (navigator.userAgent.match(/IEMobile/i) || navigator.userAgent.match(/WPDesktop/i))
    },
    any: function () {
        return (
            isOnClient() &&
            (isMobile.Android() || isMobile.BlackBerry() || isMobile.iOS() || isMobile.Opera() || isMobile.Windows())
        )
    },
}

export function getQueryParameter(name) {
    let params = getQueryParameters()
    if (name in params) {
        return params[name]
    } else {
        return null
    }
}

export function getQueryParameters() {
    if (isOnClient()) {
        return parseQuery(window.location.search)
    } else {
        return {}
    }
}

export function parseQuery(queryString) {
    const query = {}
    const pairs = (queryString[0] === "?" ? queryString.substr(1) : queryString).split("&")
    for (let i = 0; i < pairs.length; i++) {
        const pair = pairs[i].split("=")
        query[decodeURIComponent(pair[0])] = decodeURIComponent(pair[1] || "")
    }
    return query
}

export function getBaseURL() {
    if (isOnClient()) {
        const url = window.location
        return url.protocol + "//" + url.host
    } else {
        return "https://www.yogakasha.de"
    }
}

let hasOwn = {}.hasOwnProperty

export function classNames() {
    let classes = []

    for (let i = 0; i < arguments.length; i++) {
        let arg = arguments[i]
        if (!arg) continue

        let argType = typeof arg

        if (argType === "string" || argType === "number") {
            classes.push(arg)
        } else if (Array.isArray(arg) && arg.length) {
            let inner = classNames.apply(null, arg)
            if (inner) {
                classes.push(inner)
            }
        } else if (argType === "object") {
            for (let key in arg) {
                if (hasOwn.call(arg, key) && arg[key]) {
                    classes.push(key)
                }
            }
        }
    }

    return classes.join(" ")
}

export function isValidEmail(str) {
    const re =
        /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
    return re.test(String(str).toLowerCase())
}

